import { useState, useEffect } from "react";

const usePattern = (category) => {
  const [arr, setArr] = useState([]);

  useEffect(() => {
    switch (category) {
      case "field":
        setArr([
          `(||||||||||||)`,
        ]);
        break;

      case "period":
        setArr([
          `((\\d{4}(|\\.|-|\\/)( |))?([1-9]|0[1-9]|1[012])(|\\.|-|\\/)( |)([12][0-9]|3[01]|[1-9]|0[1-9])( ||\\.| )?(\\((||||||)()?\\))?(( |)|( |)~( |)|( |)-( |))(\\d{4}(|\\.|-|\\/)( |))?([1-9]|0[1-9]|1[012])(|\\.|-|\\/)( |)([12][0-9]|3[01]|[1-9]|0[1-9])(| ||\\.| )?(\\((||||||)()?\\))?|()?)`,
        ]);
        break;

      case "age":
        setArr([
          `((?<!(\\d{2,3})(-|\\)|\\) ))\\d{4}(|)?( |)(~|-)( |)\\d{4}(|)?|(( |) )?(| )\\d+(( |)|( |))?|(\\d{4}|\\d{2})()? (\\d{4}|\\d{2})()?|\\d{2}|\\d{2}( |)(~|-)( |)\\d{2}(|))`,
        ]);
        break;

      case "gender":
        setArr([
          `((?<=[^-])(||)((?=[^-]))|(?<=[^-])(||)(?=[^-])|||)`,
        ]);
        break;

      case "qualification":
        setArr([`(|)`]);
        break;

      case "contact":
        setArr([
          `([\\w-\\.]+@([\\w-]+\.)+[\\w-]{2,3}||||||||)`,
        ]);
        break;

      case "url":
        setArr([
          `(https?:\\/\\/(?:www\\.|(?!www))[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\\.[^\\s]{2,}|www\\.[a-zA-Z0-9][a-zA-Z0-9-]+[a-zA-Z0-9]\\.[^\\s]{2,}|https?:\\/\\/(?:www\\.|(?!www))[a-zA-Z0-9]+\\.[^\\s]{2,}|www\\.[a-zA-Z0-9]+\\.[^\\s]{2,})`,
        ]);

      default:
        break;
    }
  }, []);

  return arr;
};

export default usePattern;
